"use client";
import { useState, useEffect, FormEvent } from "react";
import { useSession } from "next-auth/react";
import { useRouter }  from "next/navigation";
import Calendar       from "react-calendar";
import "react-calendar/dist/Calendar.css";
import { Container, Form, Button, Alert, Row, Col } from "react-bootstrap";
import { loadStripe } from "@stripe/stripe-js";

const INSCRIPTION_PRICE = process.env.NEXT_PUBLIC_INSCRIPTION_PRICE!;  
const SERVICES = [
  { slug:"agua", label:"Estimulaci√≥n en agua", price: INSCRIPTION_PRICE },
  /* ‚Ä¶otros servicios con su priceId‚Ä¶ */
];

export default function ReservasForm() {
  const { data: session, status } = useSession();
  const router = useRouter();
  const [servicio, setServicio]   = useState("");
  const [fecha,    setFecha]      = useState<Date|null>(null);
  const [hora,     setHora]       = useState<number|null>(null);
  const [error,    setError]      = useState<string|null>(null);
  const [hasInscription, setHas]  = useState(false);

  useEffect(() => {
    if (status==="unauthenticated") router.replace("/login");
  }, [status, router]);

  useEffect(() => {
    if (session) {
      fetch("/api/user/has-inscription")
        .then(r=>r.json()).then(d=>setHas(d.hasInscription))
        .catch(()=>setHas(false));
    }
  }, [session]);

  const handleSubmit = async (e:FormEvent) => {
    e.preventDefault();
    if (!servicio||!fecha||hora===null) {
      setError("Completa todos los campos"); return;
    }
    // si es ‚Äúagua‚Äù y NO tiene inscripci√≥n, primero vamos a pagar inscripci√≥n
    if (servicio==="agua" && !hasInscription) {
      const stripe = await loadStripe(process.env.NEXT_PUBLIC_STRIPE_PUBLISHABLE_KEY!);
      return stripe!.redirectToCheckout({ lineItems:[{ price: INSCRIPTION_PRICE, quantity:1 }], mode:"payment",
        successUrl:`${process.env.NEXT_PUBLIC_APP_BASE}/dashboard?tab=reservar`,
        cancelUrl: `${process.env.NEXT_PUBLIC_APP_BASE}/dashboard?tab=reservar`
      });
    }
    // sino, pago normal
    const svc = SERVICES.find(s=>s.slug===servicio)!;
    const { sessionId } = await fetch("/api/stripe/checkout", {
      method:"POST", headers:{"Content-Type":"application/json"},
      body: JSON.stringify({ lineItems:[{price:svc.price,quantity:1}] })
    }).then(r=>r.json());
    const stripe = await loadStripe(process.env.NEXT_PUBLIC_STRIPE_PUBLISHABLE_KEY!);
    await stripe!.redirectToCheckout({ sessionId });
  };

  if (status==="loading"||!session) return <Container>Cargando‚Ä¶</Container>;

  return (
    <Container className="dashboard-container py-5">
      <h2 className="dashboard-header">Reservar cita</h2>
      {error && <Alert variant="danger">{error}</Alert>}
      <Form onSubmit={handleSubmit}>
        <Form.Group className="mb-3">
          <Form.Label>Servicio</Form.Label>
          <Form.Select
            value={servicio}
            onChange={e=>setServicio(e.target.value)}
            required
          >
            <option value="">Selecciona un servicio</option>
            {SERVICES.map(s=>(
              <option key={s.slug} value={s.slug}>{s.label}</option>
            ))}
          </Form.Select>
        </Form.Group>

        <Form.Group className="mb-4">
          <Form.Label>Fecha</Form.Label>
          <Calendar
            onChange={(d)=>setFecha(Array.isArray(d)?d[0]:d)}
            value={fecha ?? new Date()}
            minDate={new Date()}
            className="mb-3"
          />
        </Form.Group>

        {fecha && (
          <Form.Group className="mb-4">
            <Form.Label>Hora</Form.Label>
            <Row>
              {[...Array(9)].map((_,i)=>10+i).map(h=>(
                <Col xs={4} md={3} key={h}>
                  <Button
                    className={`w-100 slot-btn ${hora===h?"selected":""}`}
                    onClick={()=>setHora(h)} type="button"
                  >{h}:00</Button>
                </Col>
              ))}
            </Row>
          </Form.Group>
        )}

        <Button type="submit" className="w-100 btn-orange">
          {servicio==="agua" && !hasInscription
            ? "Pagar inscripci√≥n"
            : "Confirmar y pagar"
          }
        </Button>
      </Form>
    </Container>
  );
}
